<template>
	<div class="base">
		<div :class="{elements:true, selected: isSelected}" @click.stop="onClick" v-bind:style="{height:height+'px'}">
			<button @click="toggleChildren"> {{button_text}} </button>
			<textarea type="text" v-model:value="text"/> </textarea>
		</div>

		<div v-if="children_instantiated" :class="{childrenContainer:true, hidden : button_text=='>' }" >
			<NoteInstance v-for="child in children" :note_id="child" :parent_note_id="note_id"/>
		</div> 

	</div>
</template>

<script>
import {mapState, mapGetters} from 'vuex'

const uuidv1 = require('uuid/v1');
export default {

	name: 'NoteInstance',
	
	components: {
	},

	props:{
		note_id: String,
		parent_note_id: String
	},

	data(){
		
		return {
			button_text: '>',

			children_instantiated : false,

			is_root : false,

			height : 10
		}
	},

	methods: {

		onClick(e){
			this.$emit('click',{instance: this, event: e});
		},

		toggleChildren(){
			this.children_instantiated = true; 
			this.button_text = (this.button_text == '>' ? 'v' : '>');

		},

		
		isVisible(){
			if (this.$parent.$children.length == 0){return false;}
			if (this.$parent == this.root){return true;}
			return (this.$parent.isVisible() && this.$parent.button_text == 'v')
		},

		previousSibling(){return this.siblings[(this.index+1)%this.siblings.length]},

		//Calling on last sibling returns first one.
		nextSibling(){return this.siblings[(this.index+1)%this.siblings.length]},

		expand(){
			if(this.isFolded){this.toggleChildren()}
		},

		isLastSibling(){return this.index == this.siblings.length-1},

		path(){
			return this.$parent.path().concat([this]);
		},

		indexAsSibling(){return this.$parent.$children.indexOf(this)},

		wasDeleted(){ return this.indexAsSibling() == -1},

		resize(){
			if(this.height<this.textArea.scrollHeight){this.height = this.textArea.scrollHeight};
			
			//When deleting a line, scrollHeight is much higher than it should be. This is a workaround.
			if(this.height>this.textArea.scrollHeight){
				this.height = 0;
				this.$nextTick(function(){
					this.height = this.textArea.scrollHeight;
				}.bind(this));
			};
		}

	},
 
	computed: {
		...mapState({
			children(state){
				return state.notes[this.note_id].children;
			},

			parentNote(state){
				return this.$parent.note;
			},

			note(state){
				return state.notes[this.note_id];
			}
		}),

		show_children(){return this.children_instantiated && this.button_text=='v'},

		primaryDiv(){return this.$el.children[0]},

		textArea(){return this.$el.children[0].children[1]},

		siblings(){return this.$parent.$children},

		index(){return this.indexAsSibling()},

		isFolded(){
			return this.button_text == '>';
		},

		focusedElement: {
			set(element){
				this.root.focusedElement = element;
			},
			get(){
				return this.root.focusedElement;
			}
		},
		
		text: {
			set(text){
				this.$store.commit('setText', {id: this.note_id, text});
				if(this.height != this.textArea.scrollHeight){this.resize()};
			},
			get(){
				this.$nextTick(this.resize.bind(this));
				return this.$store.getters.getText(this.note_id);

			}
		},

		root(){
			if (this.note_id == 'root'){return this;}
			return this.$parent.root;
		},

		isSelected(){
			return this.root.instanceIsSelected(this);
		},

		isFocused(){
			return this.root.focusedInstance == this;
		},

		isExpanded() { return this.button_text == 'v'}

	},

	mounted(){

		this.$on('click',function({instance, event}){
			this.$parent.$emit('click',{instance, event});
		});
	}
};

</script>
<style scoped>

	textarea {
		height: auto;
		overflow-y: auto;
		width: 100%;
		padding: 0;
		overflow-y: hidden;
		padding-left:3px;

		font-family: sans-serif;

		-webkit-box-sizing: border-box; /* Safari/Chrome, other WebKit */
	    -moz-box-sizing: border-box;    /* Firefox, other Gecko */
	    box-sizing: border-box;    

	}

	button,textarea {
		border: 1px solid black;
	}

	button {
		border-right: none;
	}

	.base {
		float: right;
		width: 98%;
		display: block;
		padding-top: 2px;
	}	

	.elements{
		min-height: 3em;
		width: 100%;
		display: flex;	
	}

	.selected{ 
	    box-shadow: 0 0 10px red;
	}

	.hidden{
		display: none;
	}

</style>